@import 'styles/colors.scss';

.modal {
  padding: 6.5rem 0;
  display: flex;
  justify-content: center;

  &__content {
    padding: 1.5rem;
    position: relative;
    box-sizing: border-box;
    //min-width: 30rem;
    max-width: 40rem;
    background-color: $White;
    box-shadow: 0 9px 46px 5px $Black80;
    border-radius: 0.25rem;
  }

  &__close-btn {
    border: 0;
    position: absolute;
    top: -0.75rem;
    right: -0.75rem;
    background-color: $White;
    border-radius: 50%;
    box-shadow: 0 0 0.5rem 0 rgba(0, 0, 0, 0.5);
    width: 2rem;
    height: 2rem;
    padding: 0.5rem;
    cursor: pointer;
  }

  &__header {
    font-size: 1.25rem;
    font-weight: bold;
  }

  &__body {
    flex: 1 1 auto;
    padding: 1rem 0;
  }

  &__footer {
    display: flex;
    margin-top: 1.5rem;

    &--justifyContent-left {
      justify-content: left;
    }

    &--justifyContent-flex-end {
      justify-content: flex-end;
    }
  }
}
