@import 'styles/colors';
@import 'styles/mixins';

.layout {
  flex: 1 0 100%;
  min-width: 0;
  min-height: 100vh;

  display: flex;
  flex-direction: column;
  align-items: stretch;

  &__content {
    flex: 1 1 auto;

    &--dark {
      background-color: $Blue;
    }

    &-inner {
      @include inner;
    }
  }
}
