.auth-buttons-group {
  &__btn-group {
    display: flex;
    width: 100%;
  }

  &__btn {
    width: fit-content;

    &:nth-child(1) {
      margin-right: 8px;
      background-color: transparent;
      border: none;
      outline: none;
    }

    &:nth-child(2) {
      height: 32px;
      font-size: 14px;
      line-height: 14px;
      text-align: center;
      padding: 9px 16px;
      background: #2d60e0;
      border-radius: 2px;
      border: none;
      display: inline-block;
    }
  }
}
