.form {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;

  &--inline {
    flex-direction: row;
  }

  &__group-container {
    display: flex;
    justify-content: space-between;
  }

  &__group {
    display: flex;
    flex-direction: column;

    &:not(:last-child) {
      margin-bottom: 0.5rem;
    }
  }
}
