.extend-area {
  width: 100%;
  box-sizing: border-box;
  color: var(--main-color);
  list-style-type: none;
  padding: 8px 0;
  margin: 0;

  li {
    padding: 0;
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    font-size: 14px;
    line-height: 20px;
    color: #1f2533;
    height: unset;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    white-space: nowrap;
    cursor: pointer;
  }

  &__header {
    color: #1e2329;
    font-weight: 600;
  }

  &__footer {
    color: #1e2329;
    padding: 10px 18px !important;
  }

  &__line {
    height: 1px;
    width: calc(100% - 36px);
    background: #edf0f7;
    margin: 6px auto;
    box-sizing: border-box;
  }

  &__link {
    text-decoration: none;
    color: #1f2533;
    width: 100%;
    height: 100%;
    padding: 10px 16px;
  }
}
