.btn {
  display: block;
  //border: 1px solid $Blue20;
  border-radius: 3px;
  //text-transform: uppercase;
  //background-color: var(--main-button-color);
  color: var(--main-color);
  font-size: 14px;
  line-height: 18px;
  transition: background-color 0.2s, border-color 0.2s;
  text-align: center;
  padding: 8px 15px;
  width: 100%;

  &:hover:not(:disabled) {
    cursor: pointer;
    //background-color: $LightBlue20;
    //border-color: $Blue20;
  }

  &:active {
    //background-color: $LightBlue20;
    opacity: 0.85;
  }

  &:disabled {
    cursor: no-drop;
    opacity: 0.5;
  }

  svg {
    margin-right: 0.5rem;
    vertical-align: middle;
  }
}
