.extend-assets {
  width: 100%;
  box-sizing: border-box;
  color: var(--main-color);
  list-style-type: none;
  padding: 8px 0;
  margin: 0;

  li {
    padding: 0;
    display: -webkit-box;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    font-size: 14px;
    line-height: 20px;
    color: #1f2533;
    height: unset;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    white-space: nowrap;
    cursor: pointer;
  }

  &__link {
    text-decoration: none;
    color: black;
    width: 100%;
    height: 100%;
    padding: 10px 16px;
  }
}
