@import 'styles/colors';

.dropdown {
  position: relative;
  user-select: none;

  &__wrapper {
    display: flex;
    cursor: pointer;
  }

  &__btn {
    display: flex;
    align-items: center;
    border-radius: 4px 0 0 4px;

    & svg {
      height: 12px;
      width: 12px;
      fill: white;
      margin-top: 3px;
      margin-right: 6px;
    }
  }

  &__btn-icon {
    display: inline-block;
    height: 20px;
    width: 20px;

    & svg {
      margin-top: 1px;
    }
  }

  &__btn-text {
    margin: 0 0.2rem;
  }

  &__pop-up {
    position: absolute;
    margin-top: 13px;
    margin-left: -25px;
    width: 200px;
    background-color: white;
    border-radius: 4px;
    box-shadow: 0 4px 16px 0 rgba(27, 67, 88, 0.2);
    z-index: 9;
  }

  li {
    color: #445c70;
    padding: 1rem;

    &:hover {
      background-color: #f2f6fb;
      cursor: pointer;
    }
  }

  &--opened {
    .dropdown__icon {
      transform: rotate(180deg);
      border: 1px solid #e3e9f2;
      border-right: none;
      border-radius: 4px 0 0 4px;
    }
  }
}
