@import 'styles/colors';

.navbar-user {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: $Black;
  min-width: 135px;

  &__hamburger {
    svg {
      fill: $Blue;
    }

    &--authenticated {
      svg {
        fill: $White;
      }
    }

    @media only screen and (min-width: 1281px) {
      display: none;
    }
  }

  &__nav-group {
    display: flex;

    @media only screen and (max-width: 1280px) {
      display: none;
    }
  }

  &__btn {
    width: fit-content;

    &:nth-child(1) {
      margin-right: 8px;
      background-color: transparent;
      border: none;
      outline: none;
    }

    &:nth-child(2) {
      height: 32px;
      font-size: 14px;
      line-height: 14px;
      text-align: center;
      padding: 9px 16px;
      background: #2d60e0;
      border-radius: 2px;
      border: none;
      display: inline-block;
    }
  }

  &__dropdown-group {
    display: flex;
    color: $White;

    & > * {
      margin-right: 20px;
    }
  }

  &--authenticated {
    color: $White;
  }
}
