@import 'styles/colors';

.navbar-modal {
  justify-content: flex-end;
  min-height: 100%;
  padding: 0;
  box-sizing: border-box;

  &__nav-list {
    display: flex;
    flex-direction: column;
  }

  &__nav-link {
    font-size: 18px;
    padding: 16px 0;
    cursor: pointer;
    color: $White;
  }

  &__btn {
    color: $White;
  }

  &__header {
    display: flex;
    justify-content: flex-end;
  }

  &__hamburger {
    display: flex;
    justify-content: flex-end;
  }

  &__content {
    width: 100%;
    max-width: 376px;
    background-color: $Blue;
    padding: 8px 24px;
  }

  &__overview {
    display: flex;
    align-items: center;
    width: 100%;
    box-sizing: border-box;
    padding: 5px 0 5px 0px;
    color: $White;

    &:hover {
      cursor: pointer;
      color: #c4c2c2;

      svg {
        fill: #c4c2c2;
      }
    }
  }

  &__user-info {
    color: $White;
    padding-left: 18px;
  }

  &__text-main {
    font-weight: 600;
    font-size: 18px;

    &_sub {
      padding: 5px 0;
      font-size: 14px;
      font-weight: 400;
    }
  }

  &__text-secondary {
    font-size: 13px;
    line-height: 1.5px;
    margin-top: 5px;
    color: $White;

    & span:nth-child(1) {
      margin-right: 15px;
    }
  }

  &__icon {
    padding-right: 15px;

    svg {
      fill: white;
    }
  }
}
